.orderItem {
  margin: .5rem;
  .drinkTitle {
    font-weight: bold;
    font-size: 1rem;
  }
  .drinkPrice {
    float: right;
  }
  .drinkNotes {
    white-space: pre-line;
    word-break: break-all;
  }
  .actionArea {
    margin: 2rem 0 1rem 0;
    .actionLink {
      margin-right: .5rem;
      padding: .5rem;
      border: 1px black solid;
      .actionIcon {
        vertical-align: middle;
      }
    }
  }
  &:after {
    content: '';
    display: block;
    border-bottom: 1px black solid;
  }
}
